export default {
  name: 'UniTitle',
  props: {
    type: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    subTitle: {
      type: String,
      default: ''
    },
    sticky: {
      type: Boolean,
      default: false
    },
    stickyTop: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {}
  },
  watch: {
    title(newVal) {
      if (uni.report && newVal !== '') {
        uni.report('title', newVal)
      }
    }
  },
  methods: {
    onClick() {
      this.$emit('click')
    }
  }
}
